export const getKeycloakToken = () => {
  if (
      window &&
      window.entando &&
      window.entando.keycloak &&
      window.entando.keycloak.authenticated
  ) {
    return window.entando.keycloak.token;
  }
  return '';
};

export const getCookie = name => {
  return document.cookie && document.cookie.split('; ').find(row => row.startsWith(name + '=')) ?
      document.cookie.split('; ').find(row => row.startsWith(name + '=')).split('=')[1]
      : null;
};

export const getDefaultOptions = () => ({
  headers: new Headers({
    // Authorization: buildBasicAuthHeader('kagosi7953@sdysofa.com','Testtest1!'),
    // Authorization: `Bearer ${getKeycloakToken()}`,
    'Content-Type': 'application/json',
    'Cookie' : getCookie('idOperator')
  }),
});

const buildBasicAuthHeader = (username, password) => {
  return "Basic " + btoa(username + ":" + password);
};

export function getSpeedLimit(baseUrl) {
  const url = baseUrl + '/speedLimit';
  return fetch(url, getDefaultOptions()).then(res => {
    if (res.status === 401) {
      window.location.replace('/entando-de-app/en/login.page');
    }
    return res.json();
  })
}
