import React, {useEffect, useState} from 'react';
import {getSpeedLimit} from './http/api';

export default function OctoCard(props) {
  const [speedValue, setSpeedValue] = useState(0);
  const [speedUnits, setSpeedUnits] = useState(0);
  const [loaded, setLoaded] = useState(false);
  useEffect(() => {
    let mounted = true;
    getSpeedLimit(props.url).then(res => {
      setLoaded(true);
      if (mounted) {
        setSpeedValue(res.payload.value);
        setSpeedUnits(res.payload.units);
      }
    });
    return () => {
      mounted = false;
      setLoaded(false);
    };
  }, []);

  return (
      <div className="card-body">
        <div className="row no-gutters align-items-center">
          <div className="col mr-2">
            <div className="text-xs font-weight-bold text-primary text-uppercase mb-1">
              Average Speed
            </div>
            <div className="h5 mb-0 font-weight-bold text-gray-800">{loaded ? speedValue + ' ' + speedUnits : 'N/A'}</div>
          </div>
          <div className="col-auto">
            <i className="fas fa-tachometer-alt fa-2x text-gray-300"/>
          </div>
        </div>
      </div>
  );
}
